@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');

.codebox {
    background-color: #1a1a1a;
    color: #c1c1c1;

    border-radius: 10px;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);

    position: relative;
    display: inline-block;

    font-family: 'Roboto Mono', monospace;
}

.codebox-left {
    transform: perspective(1000px) rotateY(-15deg) rotateX(5deg) rotate(1deg) scaleY(.9) scaleX(.95) translateX(-3%) translateY(-3%);
    transition: transform 0.4s cubic-bezier(.2, 1, 1, 1);
}

.codebox-right {
    transform: perspective(1000px) rotateY(15deg) rotateX(5deg) scaleY(.9) scaleX(.95) translateX(3%) translateY(-3%);
    transition: transform 0.4s cubic-bezier(.2, 1, 1, 1);
}

.codebox:hover {
    transform: perspective(1000px) rotateY(0deg) rotateX(0deg) rotate(0deg) scale(1) translateX(0) translateY(0) !important;
}

.codeboxTitlebar {
    position: relative;

    background-color: rgb(18, 18, 18);
    padding: 8px 10px;
    font-size: 14px;

    border-radius: 10px 10px 0px 0px;
}

.codeboxTitlebarTitle {
    text-align: center;
    display: block;
}

.codeboxTextbox pre {
    margin: 0;
}

.codeboxTextbox pre code {
    padding: 0;
    background-color: rgba(0, 0, 0, 0);

    padding: 18px;
    margin-right: 10px;

    font-family: 'Roboto Mono', monospace;
    font-size: 16px;
    line-height: 1.8;
}

.codeboxTextbox pre code a {
    padding: 0;
    display: block;

    width: 100%;
}

.codeboxTitlebarBall {
    position: absolute;
    width: 13px;
    height: 13px;
    top: 10px;

    border-radius: 100%;
}

.codeboxTitlebarBall:hover {
    filter: brightness(0.8);
}

#ballA {
    background-color: #ff6f6f;
}

#ballB {
    background-color: #ffef74;
    left: 32px;
}

#ballC {
    background-color: #b8ff8b;
    left: 54px;
}

.codeboxTextbox {
    position: relative;
}

.codeboxTitlebarCopy {
    position: absolute;
    top: 4px;
    right: 8px;
    padding: 2px 10px;
    border-radius: 2px;

    border: 2px solid #2f2f2f;

    cursor: pointer;
    user-select: none;

    opacity: 0;
    transition: opacity 1s;
}

.codeboxTitlebarCopy:hover {
    background-color: #2f2f2f;
}